import React, { Component } from 'react';
import { connect } from 'react-redux';
import { change } from 'redux-form';
import { Container } from 'css/basicElements';
import LookUpForm from './Form';
import Logo from './Logo';
import OutputBox from './output/OutputBox';
import { lookupMap,fetchRegistry } from '../connectors/actions/index';
import { palette } from 'css/variables';

class Home extends Component {

    componentDidMount () {
        const { location } = this.props;
        this.getParams(location);
    }

    getParams = (location) => {
        const { dispatch } = this.props;
        const searchParams = new URLSearchParams(location.search);
        if (searchParams.get('sn')) {
            dispatch(change('lookupForm', 'sn', searchParams.get('sn')));
            dispatch(lookupMap(dispatch,searchParams.get('sn')));
        }
        if (searchParams.get('cmd') === 'hello') {
            dispatch({
                type: 'INDICATE_CARD_LOAD',
                })
          }
          dispatch(fetchRegistry(dispatch))
    }

    render() {
            return (
                <div>
                    <header className="app-header">
                        <Container>
                        <div className="logo-wrapper">
                        <Logo
                            fill={palette.green}
                            width={320}
                            style={{zIndex:-1}}
                          />
                        </div>
                          
                    
                            <div>
                            <LookUpForm />
                          </div>
                        </Container>
                    </header>
                    <section className="output">
                        <Container className="container">
                            <OutputBox />
                        </Container>
                    </section>
                </div>
        )
    }
};

export default connect(
    state => ({
        form: state.form,
        state,
    })
)(Home);



