import React from 'react';
import { connect } from 'react-redux';
import { Container } from 'css/basicElements';
import Logo from '../Logo'
import { palette } from 'css/variables';

let About = props => {
    return (
      <Container>
        <div>
          <div style={{float:"right"}}>
            <a href="/">H18 Home</a><br />
            <a href="/about/h18">About Helsinki 2018</a><br />
            <a href="/about/usecases">About Use Cases</a><br />
            <a href="/about/resolver">About Resolver</a><br />
            <a href="/about/h18method">About H18 Method</a><br />
            <a href="/about/h18cards">About Cards</a>
          </div>
          <a href="/">
          <div className="logo-wrapper">
          <Logo
              fill={palette.green}
              width={320}
              style={{zIndex:-1}}
            />
            <div style={{color:"white"}}>
            Helsinki 2018
            </div>

          </div>
          </a>
        </div>
        <h4>About : {props.title}</h4>
        <hr />
        {props.children}
      </Container>
    )
}

About = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(About);


export default About;



