import { combineReducers } from 'redux';

// Reducers
import { reducer as formReducer } from 'redux-form';
import server from './server';
import qrcode from './qrcode';

const reducer = combineReducers({
  server,
  qrcode,
  form: formReducer,
})

export default reducer



