import React from 'react';
import { connect } from 'react-redux';
import About from './About';

let AboutH18Cards = props => {
      return (
        <About title="Helsinki 2018 Cards and NoT - the Non-Internet of Things">
          It turns out that many things which have identities are not smart
          phones and lack both human biometrics and secure cryptographic
          co-processing enclaves.  They lack GPS tracking signals, and you can
          not easily run their fingerprints through an AFIS system.
          <br />
          Despite these limitations, the cards continue to exist - quite happily -
          sitting and carding around with their cardly friends, hanging out in wallets
          and on desktops, nestled down in the cracks of sofa cushions or
          resting, forgotten on taxi seats or the tables of conference venues.
          <br />
          Every one of these cards was born at
          <a href="http://www.nuntapun.com/">Nuntapun Printing</a> (which is
          <a href=",19.64z/data=!4m5!3m4!1s0x30da305971986779:0x380cd0d910a54e67!8m2!3d18.7558546!4d98.9681593" >
          here</a>) and they are destined to travel the world before
          becoming irrelevant, lost, or discarded.  which is the nature of
          cardish death.
          <br />
          It is simply not easy enough to model the life of these cards, to
          track an honor their brief existence, given the tools available to
          digital and decentralized identity systems.  They are the forgotten,
          the disenfranchised, the discluded identities.
          <br />
          Yet, as perhaps the simplest reference case in the world, and in the
          pursuit of card/human relations, we at Korsimoro aim to change this.
          Along the way we hope to learn a lot about the world of DIF and
          understand which tools are card-positive, and which are cardist, which
          support limited identities lacking smart-devices and the ability to
          access the internet and express agency over their private keys.
          <br />
          The more serious side....
          <ol>
            <li></li>
          </ol>
        </About>
      )
}

AboutH18Cards = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutH18Cards);


export default AboutH18Cards;



