import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Form, Field, reduxForm } from 'redux-form';
import { FormGroup, InputGroup, SelectWrapper, Button, Label } from 'css/formElements';

import { resetOutputBox, submitValues } from 'connectors/actions';

import {alphaNumeric, maxLengthLimit, minLengthLimit, renderField } from './validation';
const resolvers = ['h18', 'sov', 'btcr','dom','erc275','ipid','stack','uport','v1'];

class LookUpForm extends Component {
    render () {
        const {  didChange, methodChange, pristine, handleSubmit, onSubmit, submitting, formValues } = this.props;

        let activeMethod;
        if (formValues.lookupForm)
            if (formValues.lookupForm.values)
                activeMethod = formValues.lookupForm.values.method;
        return (
            <Form onSubmit={handleSubmit}>
                <FormGroup>
                    <SelectWrapper>
                    <Label htmlFor="method">DID method</Label>
                        <Field
                            name="method"
                            component="select"
                            onChange={methodChange}
                        >
                            {resolvers.map((resolver, i) => (
                                <option key={i}>
                                    {resolver}
                                </option>
                            ))}
                        </Field>
                    </SelectWrapper>
                    <InputGroup>
                        <Field
                            name="sn"
                            type="text"
                            component={renderField}
                            validate={[alphaNumeric, maxLengthLimit, minLengthLimit]}
                            maxLength={maxLengthLimit}
                            label="Identifier"
                            onChange={didChange}
                        />
                    </InputGroup>
                    <InputGroup>
                        <Button
                            greenOutline
                            disabled={pristine || submitting || activeMethod !== 'h18' }
                            onClick={handleSubmit(values =>
                            onSubmit({
                                ...values,
                                lookup: 'map'
                            }))}

                            >
                            H18 Metadata
                        </Button>
                        <Button
                            greenOutline
                            disabled={pristine || submitting || formValues.sn === ''}
                            onClick={handleSubmit(values =>
                            onSubmit({
                                ...values,
                                lookup: 'ddo'
                            }))}>
                            DID Document
                        </Button>
                    </InputGroup>
                </FormGroup>
            </Form>
        )
    }
};

LookUpForm = reduxForm({
    form: 'lookupForm',
})(LookUpForm);

LookUpForm = connect(
    state =>({
        formValues: state.form,
        initialValues: {
            method: 'h18',
        }
    }),
    dispatch => ({
      methodChange: () => {
        dispatch(resetOutputBox())
      },
      didChange: () => {
        dispatch(resetOutputBox())
      },
      onSubmit: (values) => {
          dispatch(submitValues(dispatch,values));
      }
    })
)(LookUpForm);

export default LookUpForm;



