import styled from 'react-emotion';
import { rem, clearFix, transparentize } from 'polished';
import propStyles from 'prop-styles';
import { text, list } from 'css/utilities';
import { palette } from 'css/variables';

export const Container = styled('div')`
    max-width: 1170px;
    margin: auto;
    padding: ${rem('36px', '16px')};
`;

export const Section = styled('section')`
    position: relative;
    min-height: 100vh;
    ${clearFix};
    transform: translateY(80px);
`;

export const Article = styled('article')`
    position: relative;
    ${clearFix};
`;

export const Text = styled('p')`
    ${propStyles({
        textSm: [text.small],
        textMd: [text.medium],
        textMdLg: [text.mediumLg],
        textLg: [text.large],
        textXlg: [text.xLarge],
        textHuge: [text.huge],

        center: [text.center],
        left: [text.left],
        right: [text.right],
        justify: [text.justify],

        grayLight: [text.grayLight],
        greenNeon: [text.greenNeon],
        green: [text.green],
        greenDarker: [text.greenDarker],
        greenDarkest: [text.greenDarkest],
    })};
    position: relative;
    z-index: 2;
`;

export const H1 = styled('h1')`
    ${propStyles({
        textSm: [text.small],
        textMd: [text.medium],
        textMdLg: [text.mediumLg],
        textLg: [text.large],
        textXlg: [text.xLarge],
        textHuge: [text.huge],

        center: [text.center],
        left: [text.left],
        right: [text.right],
        justify: [text.justify],

        grayLight: [text.grayLight],
        greenNeon: [text.greenNeon],
        green: [text.green],
        greenDarker: [text.greenDarker],
        greenDarkest:[text.greenDarkest],

        upperCase:[text.upperCase],
    })};
    position: relative;
    letter-spacing: -2px;
    line-height:0.8;
    z-index: 2;
    transform: translateY(-10%);
    text-shadow: 0 0 10px ${transparentize(0.5, palette.greenDarkest)};
`;

export const List = styled('ul')`
    ${propStyles({
        noBullet: [list.none],
        square: [list.square],
        bullet: [list.bullet],
    })};

    margin: 0;
    padding: 0;
`;

export const ListItem = styled('li')`
    font-size: ${rem('18px', '16px')};
    margin: 9px 0 0 0;
    -webkit-column-break-inside: avoid;
          page-break-inside: avoid;
               break-inside: avoid;
    &:first-child {
        margin-top: 0;
    }
    h2, h3 {
        margin-top: 0;
    }
`;





