import React from 'react';
import { connect } from 'react-redux';
import About from './About';

let AboutUniversalResolver = props => {
      return (
        <About title="DID Document Resolution">
          DID document resolution is defined per method, and involves a wide
          range of different technologies.
          <br />
          The <a href="https://github.com/decentralized-identity">DIF's source
          repository.</a> contains a universal resolver, with a front end
          that can be used a <a href="http://uniresolver.io">uniresolver.io</a>.
          <br />
          As part of Korsimoro's operations assessment, we are setting up a
          separate instance of universal resolver code base, and adding a toy
          resolver for the <a href="/about/h18method">h18 toy</a> method.  This work is effectively a
          QA/testing exercise, with an eye towards making these complicated
          toolkits easier to use.  We hope that, in so doing, we can incubate
          interest and action outside of the immediate DIF community.
          <br />
          This effort will:
          <ol>
            <li>Explore and document deployment requirements</li>
            <li>Allow us to document and fix any bugs</li>
            <li>Explore the process of adding a non-registered method</li>
          </ol>
          <br />
          <img
            src="/github/universal-resolver/docs/figures/architecture.png"
          />
          (citation : https://github.com/decentralized-identity/universal-resolver/blob/master/docs/architecture-drivers.md)
        </About>
      )
}

AboutUniversalResolver = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutUniversalResolver);


export default AboutUniversalResolver;



